import { ITokenAsset } from 'constants/index'
import { parseEnv } from './helpers'

export const networkID = parseEnv('REACT_APP_NETWORK_ID', '1') as '4' | '1'
export const rpcURL = parseEnv('REACT_APP_NETWORK_URL', '')

interface INetwork {
  [chainID: string]: {
    chainId: string
    chainName: string
    rpcUrls: string[]
    iconUrls: string[]
    blockExplorerUrls: string[]
    nativeCurrency: {
      name: string
      symbol: string
      decimals: number
    }
  }
}

const networks: INetwork = {
  1: {
    chainId: '0x1',
    chainName: 'Ethereum Mainnet',
    rpcUrls: [rpcURL],
    iconUrls: [],
    blockExplorerUrls: ['https://etherscan.io/'],
    nativeCurrency: {
      name: 'Ethereum',
      symbol: 'ETH',
      decimals: 18,
    },
  },
  4: {
    chainId: '0x4',
    chainName: 'Rinkeby Testnet',
    rpcUrls: [rpcURL],
    iconUrls: [],
    blockExplorerUrls: ['https://rinkeby.etherscan.io/'],
    nativeCurrency: {
      name: 'Ethereum',
      symbol: 'ETH',
      decimals: 18,
    },
  },
  108: {
    chainId: '0x6c',
    chainName: 'Thundercore Mainnet',
    rpcUrls: ['https://mainnet-rpc.thundercore.com'],
    iconUrls: ['https://thundercore.github.io/dist/thundercore.png'],
    blockExplorerUrls: ['https://viewblock.io/thundercore'],
    nativeCurrency: {
      name: 'Thundercore Token',
      symbol: 'TT',
      decimals: 18,
    },
  },
  18: {
    chainId: '0x12',
    chainName: 'Thundercore Testnet',
    rpcUrls: ['https://testnet-rpc.thundercore.com'],
    iconUrls: ['https://thundercore.github.io/dist/thundercore.png'],
    blockExplorerUrls: ['https://viewblock.io/thundercore'],
    nativeCurrency: {
      name: 'Thundercore Token',
      symbol: 'TT',
      decimals: 18,
    },
  },
}

export function switchEthereumChain() {
  window.ethereum
    ?.request({
      method: 'wallet_switchEthereumChain',
      params: [
        {
          chainId: networks[networkID].chainId,
        },
      ],
    })
    .catch((error: any) => {
      if (error.code === 4001) {
        // EIP-1193 userRejectedRequest error
        console.log('Switch network failed')
      } else {
        console.error(error)
      }
    })
}

export function autoSwitchNetwork() {
  window.ethereum
    ?.request({
      method: 'wallet_addEthereumChain',
      params: [networks[networkID]], // you must have access to the specified account
    })
    .catch((error: any) => {
      if (error.code === 4001) {
        // EIP-1193 userRejectedRequest error
        console.log('Switch network failed')
      } else {
        console.error(error)
      }
    })
}

export function watchAsset(asset: ITokenAsset) {
  if (window.ethereum) {
    const { address, symbol, decimals, type, image } = asset
    try {
      window.ethereum.request({
        method: 'wallet_watchAsset',
        params: {
          type: type || 'ERC20',
          options: {
            address,
            symbol,
            decimals,
            image,
          },
        },
      })
    } catch (error) {
      console.log('error:', error)
    }
  }
}

export function isAddressesMatch(addr1: string, addr2: string): boolean {
  return addr1.toLowerCase() === addr2.toLowerCase()
}
